@import "../../inc/vars";
.acro-testimonials {
	padding: 0 20px;
	width: 1400px;
	max-width: 100%;
	margin: 0 auto;
	&__title {
		margin-bottom: 37px;
		@media (min-width: $screen-sm){
			margin-bottom: 83px;
		}
		h2 {
			@include titleGrad();
			@include pop(400, 30, 1.24);
			text-align: center;
			@media (min-width: $screen-sm){
				font-size: 36px;
			}
		}
	}
	&__items {
		display: flex;
		gap: 20px;
		flex-wrap: wrap;
		@media (min-width: $screen-sm){
			gap: 33px;
			flex-wrap: nowrap;
		}
		.list-item {
			width: 100%;
			background: #fff;
			padding: 17px 25px;
			@media (min-width: $screen-sm){
				width: 50%;
				padding: 32px 46px;
			}
			&__image {
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 15px;
				@media (min-width: $screen-sm){
					margin-bottom: 27px;
				}
				&-wrapper {
					img {
						width: 30px;
						@media (min-width: $screen-sm){
							width: 54px;
						}
					}
				}
			}
			&__rating {
				svg {
					width: 74px;
					path {
						fill: #D1D4D3;
					}
					@media (min-width: $screen-sm){
						width: 134px;
					}
				}
				&.rating_5 {
					svg {
						path {
							fill: #27B989;
						}
					}
				}
				&.rating_4 {
					svg {
						path {
							&:nth-child(1),
							&:nth-child(2),
							&:nth-child(3),
							&:nth-child(4) {
								fill: #27B989;
							}
						}
					}
				}
				&.rating_3 {
					svg {
						path {
							&:nth-child(1),
							&:nth-child(2),
							&:nth-child(3) {
								fill: #27B989;
							}
						}
					}
				}
				&.rating_2 {
					svg {
						path {
							&:nth-child(1),
							&:nth-child(2) {
								fill: #27B989;
							}
						}
					}
				}
				&.rating_1 {
					svg {
						path {
							&:nth-child(1) {
								fill: #27B989;
							}
						}
					}
				}
			}
			.listContent {
				@include fig(400, 14, 1.24);
				@media (min-width: $screen-sm){
					font-size: 19px;
				}
			}
			&__description {
				margin-bottom: 12px;
			}
			&__author {
				font-size: 14px;
				font-weight: 600;
				@media (min-width: $screen-sm){
					font-size: 21px;
				}
			}
		}
	}
}